import React from 'react';
import Slider from 'react-slick';

class MySlider extends React.Component {

  state = {}

  componentDidMount(){

    console.log('slider mounted');

    this.setState({
      imgUrls: [
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSkmXWOwBDnq0ZBqto7ZOWrFo1Lbb7gnUuXoA&usqp=CAU',
        'https://static.food4rhino.com/s3fs-public/styles/large/public/users-files/zemtsov/app/screenshot_2.png',
        'https://api.twilio.com/2010-04-01/Accounts/ACee6847b8e1157c9809f9d733f93d0f39/Messages/MM051bbd7cb98749688c5ebf39e22ad5da/Media/ME121855e27e7eefd80fcdab396fdec673',
        'https://api.twilio.com/2010-04-01/Accounts/ACee6847b8e1157c9809f9d733f93d0f39/Messages/MM04cd6bee81e585969365e67aee7f95e4/Media/ME36a9eda27e3e95ab5620423bee7ddf48',
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSkmXWOwBDnq0ZBqto7ZOWrFo1Lbb7gnUuXoA&usqp=CAU',
        'https://static.food4rhino.com/s3fs-public/styles/large/public/users-files/zemtsov/app/screenshot_2.png'
      ]});

    };

  // https://react-slick.neostack.com/docs/api/
  settings = {
    autoplay: true,
    dots: false,
    infinite: true,
    speed: 500,
    autoplayspeed: 200,
    slidesToShow: 2,
    slidesToScroll: 1
  };

  render() {
    if (this.state.imgUrls) {
      return (
        <Slider {...this.settings}>
          { this.state.imgUrls.map(url => <div key={'div_' + url}><img src={url} key={url} alt={'alt' + url} width={'95%'} height={'95%'} /></div>) }
        </Slider>
      );
    } else {
      return <p>Getting photos...</p>
    };
  };

};


function App() {
  return <MySlider />
};

export default App;
